.button {
    font-family:"Dancing Script";
    font-size:16px;
    background-color: black;
    border-radius: 15px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-decoration: none;
    color: #FFFFFF;
    font-size: 16px;
    border: 0px;
}

.button:hover {
    font-family:"Dancing Script";
    font-size:16px;
    background-color:fuchsia;
    border-radius: 15px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    transition: 120ms;
    text-decoration: none;
    color: #FFFFFF;
    border: 0px;
}